class AnalyzedRepositoriesComponent {
    constructor(dashboardService) {
        this.dashboardService = dashboardService;
    }
    ngOnInit() {
        this.getAnalyzedRepos();
        this.newAnalyzeHappened = this.dashboardService.newAnalyzeHappened.subscribe(x => {
            this.getAnalyzedRepos();
        });
    }
    getAnalyzedRepos() {
        this.dashboardService.getAnalyzedRepos().subscribe((data) => {
            this.repos = data;
        });
    }
    ngOnDestroy() {
        this.newAnalyzeHappened.unsubscribe();
    }
}


